/* Global Reset and Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Inter', sans-serif;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  background: linear-gradient(to bottom right, #e0f7fa, #fce4ec);
}

body {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(to right, #f9f9f9, #e0e7ff);
  color: #1a202c;
}

/* Form Styling */
form {
  background-color: #ffffff;
  color: #1a202c;
  box-shadow: 0 20px 30px rgba(0, 0, 0, 0.1);
  border-radius: 1rem;
  padding: 2.5rem;
  width: 100%;
  max-width: 400px;
}

/* Dark Mode Support */
@media (prefers-color-scheme: dark) {
  form {
    background-color: #1f2937;
    color: #ffffff;
  }
}

/* Input fields */
input[type="email"],
input[type="password"] {
  width: 100%;
  padding: 0.75rem;
  margin-bottom: 1rem;
  background-color: #f3f4f6;
  color: #1f2937;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  outline: none;
  transition: border 0.3s, box-shadow 0.3s;
}

input[type="email"]:focus,
input[type="password"]:focus {
  border-color: #6366f1;
  box-shadow: 0 0 0 2px rgba(99, 102, 241, 0.4);
}

/* Dark mode inputs */
@media (prefers-color-scheme: dark) {
  input[type="email"],
  input[type="password"] {
    background-color: #374151;
    color: #ffffff;
    border-color: #4b5563;
  }
}

/* Labels */
label {
  display: block;
  margin-bottom: 0.25rem;
  font-size: 0.875rem;
  font-weight: 600;
  color: #374151;
}

/* Button */
button[type="submit"] {
  width: 100%;
  padding: 0.75rem 1.5rem;
  font-size: 1.125rem;
  font-weight: bold;
  color: white;
  background-color: #4f46e5;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
  box-shadow: 0 8px 20px rgba(79, 70, 229, 0.2);
}

button[type="submit"]:hover {
  background-color: #4338ca;
}

/* Form Heading */
form h1 {
  font-size: 1.875rem;
  font-weight: 800;
  text-align: center;
  margin-bottom: 1.5rem;
  color: #111827;
}

/* Dark mode heading */
@media (prefers-color-scheme: dark) {
  form h1 {
    color: #ffffff;
  }
}

/* Responsive adjustments */
@media (max-width: 480px) {
  form {
    padding: 1.5rem;
  }

  form h1 {
    font-size: 1.5rem;
  }

  button[type="submit"] {
    font-size: 1rem;
  }
}
